.search {
    padding: 20px;
    text-align: center;
}

.search-box {
    width: 100%;
    max-width: 400px;
    padding: 10px;
    margin: 10px auto 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
    color: #9651ED;
}

.search-box::placeholder {
    color: #9651ED;
}

.product-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    justify-items: center;
}

.product-card {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 15px;
    text-align: center;
    background-color: #f9f9f9;
    width: 100%;
    max-width: 300px;
}

.product-card h3 {
    color: #9651ED;
    font-size: 1.2em;
}

.product-card p {
    color: #9651ED;
}

.product-image {
    width: 100%;
    height: auto;
    max-height: 200px;
    object-fit: cover;
    border-radius: 8px;
    margin-bottom: 10px;
}
